import React from 'react';
import config from '../config';
import {Row, Col, Container} from 'react-bootstrap';

function Experience() {
	return (
		<Container className="my-card my-info">
			<p className="card-title">Experience</p>
			<Row>
				{config?.experiences?.map((item) => (
					<Col key={item?.company} sm={6}>
						<div key={item?.company} className="project-holder">
							<p className="title">{item.company}</p>
							<p className="title">{item.position}</p>
							<p className="title">{`${item.from} - ${item.to}`}</p>    
						</div>
					</Col>
				))}
			</Row>
		</Container>
	);
}

export default Experience;
